@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
html{scroll-behavior:smooth}*,*::after,*::before{margin:0;padding:0;box-sizing:border-box;font-family:"Nunito",sans-serif,serif}body{background-color:#ddd;color:#212121;overflow-x:hidden}a:link,a:visited{color:inherit;text-decoration:none}ul{list-style:none}textarea{resize:none}input,textarea{border:none}input:focus,textarea:focus{outline:none}button{border:none;cursor:pointer}button:focus{outline:none}

.feed{height:92vh;padding:2rem;display:flex;flex-direction:column;align-items:center}.feed__add-ques{background-color:#fefefe;width:40%;padding:.75rem;border-radius:5px;transition:.2s all;cursor:pointer}.feed__add-ques:hover{box-shadow:0 0 2px #00000088}.feed__add-ques aside{padding:.75rem 0 0 0;font-weight:bold;color:#888;font-size:1.25rem}.feed__add-ques h2{display:flex;align-items:center;font-size:.75rem}.feed__add-ques h2 p{font-weight:normal;margin-left:.25rem}.feed__add-ques h2 img{width:1.25rem;height:1.25rem;background-color:#333;border-radius:50%}

.header{background-color:#fff;height:8vh;display:flex;align-items:center;padding:0 20rem;box-shadow:2px 2px 10px #00000011}.header *{margin:0 .5rem}.header input{display:block;border:1px solid #00000033;padding:.5rem;font-size:1rem;border-radius:5px;transition:.2s all;width:22rem}.header input:hover{border:1px solid blue}.header input:focus{border:1px solid blue;box-shadow:0 0 2px #00000088}.header img{width:2rem;height:2rem;border-radius:50%;background-color:lightblue}.header button{padding:.5rem 1rem;border-radius:50px;color:#fefefe;background-color:#c7322c;min-width:-webkit-max-content;min-width:-moz-max-content;min-width:max-content}.header button:hover{background-color:#A82723}

.profile{height:92vh;padding:2rem;display:flex;flex-direction:column;align-items:center}.profile main{width:80%;height:80vh;background-color:#fff;border-radius:5px;box-shadow:2px 2px 10px #00000011;display:flex;padding:3rem}.profile main section{flex:2 1}.profile main aside{flex:1 1}.profile__details{display:flex;align-items:center;border-bottom:1px solid #00000033;padding-bottom:1rem}.profile__details img{width:7rem;height:7rem;border-radius:50%;background-color:black;margin-right:1.5rem}.profile__details h1{font-size:2rem}

.auth{height:100vh;display:flex;justify-content:center}.auth main{min-height:70vh;height:-webkit-max-content;height:-moz-max-content;height:max-content;width:50%;background-color:#fff;border-radius:5px;margin:2rem;padding:2rem;display:flex;flex-direction:column}.auth main h1{text-align:center;font-size:3rem}.auth main p{text-align:center}.auth main section{margin-top:1rem;flex:1 1;display:flex}.auth main section aside{flex:1 1}.auth__left{border-right:1px solid #33333333;padding:1rem 1.5rem}.auth__button{display:block;width:100%;margin:1rem 0;padding:.5rem 0;font-size:1.2rem;border:1px solid #00000033;background-color:inherit;border-radius:5px}.auth__button:hover{background-color:#44444411}.auth__button--email{border:none;border-radius:50px;font-size:1rem;padding:.35rem 0}.auth__right{padding:1rem 1.5rem}.auth h3{border-bottom:1px solid #00000033;padding-bottom:.2rem;margin-bottom:1rem}.auth__input{margin:1rem 0}.auth__input label{font-weight:bold}.auth__input input{display:block;border:1px solid #00000033;width:100%;padding:.5rem;font-size:1.2rem;border-radius:5px;transition:.2s all}.auth__input input:hover{border:1px solid blue}.auth__input input:focus{border:1px solid blue;box-shadow:0 0 2px #00000088}.auth form button{background-color:#3D5AFF;color:#fefefe;font-size:1.2rem;border-radius:500px;padding:.5rem 1.25rem;opacity:.85;transition:.1s all}.auth form button:hover{opacity:1}.auth form button:disabled{pointer-events:none;opacity:.5}.auth__pass-tip{margin-left:.25rem;border:1px solid black;display:inline-block;text-align:center;padding:1px;width:1rem;height:1rem;border-radius:50%;font-size:.75rem;cursor:default;transform:scale(0.75)}

